<template>
  <div class="desk">
      <div class="head">
         <img src="~@/assets/desk-icons/head.png">
      </div>
      <div class="top-icon">
          <ul>
            <li>
              <img src="~@/assets/desk-icons/desk_icon1.png">
              <div class="txt">中国移动</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon2.png">
              <div class="txt">和包支付</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon3.png" @click="chat" id="shakeTarget">
              <div class="txt">和飞信</div>
              <div class="hand animation-hand"><img src="~@/assets/hand.png"></div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon4.png">
              <div class="txt">中移党建云</div>
            </li>
             <li>
              <img src="~@/assets/desk-icons/desk_icon5.png">
              <div class="txt">小移人家</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon6.png">
              <div class="txt">相机</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon7.png">
              <div class="txt">相册</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon8.png">
              <div class="txt">天气</div>
            </li>
          </ul>   
      </div>
      <div class="desk-point"><img src="~@/assets/desk-icons/desk_point.png"></div>
      <div class="bottom-icon">
          <ul>
          <li>
              <img src="~@/assets/desk-icons/desk_icon9.png">
              <div class="txt">电话</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon10.png">
              <div class="txt">联系人</div>
            </li>
            <li>
              <img src="~@/assets/desk-icons/desk_icon11.png">
              <div class="txt">浏览器</div>
            </li>
            </ul>   
      </div>
  </div>
</template>

<script>
export default {
  name: 'desk',
  data () {
    return {}
  },
  methods:{
    chat(){
      this.$parent.status=2;
    }
  },
  mounted(){
    this.$nextTick(function(){
      var $imgs=document.querySelectorAll(".top-icon li img,.bottom-icon li img");
      $imgs.forEach(function($img,index){
          $img.onclick=function(){
              clearTimeout(time);
              var classList=document.getElementById("shakeTarget").classList;
              classList.add("shake");
               var time=setTimeout(function(){
                  classList.remove("shake");
               },1000);
            }
      })
    })
  }
}
</script>
<style lang="less">
.desk-point{
  position: absolute;
  bottom: 2.3rem;
  left: 0;
  width: 100%;
  text-align: center;
  img{
    width: 1.5rem;
  }
}
  .desk{
    position: relative;
    height: 100%;
    background:#6791bf url('~@/assets/desk-icons/desk_bg.jpg') no-repeat;
    background-size: 100%;
    padding-bottom: 2.2rem;
    .top-icon{
      margin-top: 3.5rem;
    }
    .bottom-icon{
      position: absolute;
      overflow: hidden;
      bottom:0;
      left: 0;
      width:76%;
      margin-left: 12%;
      height: 2.2rem;
      padding-top: 0.1rem;
      li{
        width: 33.33% !important;
      }
    }
    .hand{
        position: absolute;
       top:0.7rem;
        left: 0.6rem;
        z-index: 11;
        img{
          width: 0.9rem;
        }
      }
    .head{
      padding: 0.2rem 0.4rem;
      img{
        width: 100%;
      }
    }
  }
  .top-icon,.bottom-icon{
    ul{
      position: relative;
      width: 100%;
      padding: 0 0.2rem;
      overflow: hidden;
      z-index: 1;
    }
    li{
      position: relative;
      width: 25%;
      padding: 0 0.3rem;
      margin-top: 0.1rem;
      float: left;
      .txt{
        text-align: center;
        padding-bottom: 0.15rem;
        font-size: 0.28rem;
        color: #fff;
        white-space:nowrap;
      }
      img{
        width: 100%;
      }
    }
  }
  .shake{
    animation: shake 1s;
  }
</style>



