export default function loader(imgs,callback){
	var isloader=true;
	var startPo=0;
	var imgLength=imgs.length;
	var loadTimer;
	function loadImg(startPo){
		var src=imgs[startPo];
		var loadAbj=new Image();
		loadAbj.src=src;
		loadAbj.onload=function(){
			startPo++;
			callback(startPo);
			IfLoadImg(startPo);
		}
		loadAbj.onerror=function(){
			loadImg(startPo);
		}
	}
	function IfLoadImg(startPo){
		if(startPo>=imgLength){
		}else{
	        loadImg(startPo);
		}
	} 
	IfLoadImg(startPo);
}


