export default function chatDialog(data,$elem){
	var baseurl="./static/member/";
	var member={
		me:{
			id:"me",
			name:"我",
			head:`${baseurl}me.jpg`
		},
		shangbing:{
			id:"",
			name:"尚冰（党组书记 董事长）",
			head:`${baseurl}shangbing.png`
		},
		tangxiaoyun:{
			id:"",
			name:"党小云",
			head:`${baseurl}tangxiaoyun.png`
		},
		liyue:{
			id:"",
			name:"李跃（党组副书记 总裁）",
			head:`${baseurl}liyue.png`
		},
		lizhengmao:{
			id:"",
			name:"李正茂（党组成员 副总裁）",
			head:`${baseurl}lizhengmao.png`
		},
		lihuidi:{
			id:"",
			name:"李慧镝（党组成员 副总裁）",
			head:`${baseurl}lihuidi.png`
		},
		dongxing:{
			id:"",
			name:"董昕（党组成员 副总裁）",
			head:`${baseurl}dongxing.png`
		},
		guohao:{
			id:"",
			name:"郭浩（党组成员 纪检组长）",
			head:`${baseurl}guohao.png`
		},
		jianqing:{
			id:"",
			name:"简勤（党组成员 副总裁）",
			head:`${baseurl}jianqing.png`
		},
		wangxiaoyun:{
			id:"",
			name:"王晓云（十九届中央候补委员 技术部总经理）",
			head:`${baseurl}wangxiaoyun.png`
		},
		zhaojing:{
			id:"",
			name:'赵静（十九大代表，湖北公司党员）',
			head:`${baseurl}zhaojing.png`
		},
		guolangke:{
			id:"",
			name:"郭兰珂（十九大代表 重庆公司党员）",
			head:`${baseurl}guolangke.png`
		},
		shiyuxi:{
			id:"",
			name:"石玉喜（十九大代表 中移铁通公司党员）",
			head:`${baseurl}shiyuxi.png`
		}
	};
	var emojiUrl="./static/Emoji/";
	var emoji={
		jingzhang:`<img src="${emojiUrl}jingzhang.png">`,
		bwcx:`<img src="${emojiUrl}bwcx.gif">`,
		zcgjdd:`<img src="${emojiUrl}zcgjdd.gif">`
	}
	//0 为文本  1为表情  2为红包
	var dialog=[
		{
			author:member.shangbing,
			content:"同志们，大家好！",
			dialogType:0,
		},
		{
			author:member.me,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.liyue,
			content:"7月1日马上就要到了。中国共产党自诞生以来，已经走过了97年光辉的岁月。",
			dialogType:0,
		},
		{
			author:member.me,
			content:"原来是庆祝七一呀！七一是党的生日，也是我们每一名党员的重要节日！",
			dialogType:0,
		},
		{
			author:member.shangbing,
			content:"今天我们创建这个群，就是要在党的生日来临之际，向公司全体共产党员致以诚挚的问候和节日的祝福！",
			dialogType:0,
		},
		{
			author:member.shangbing,
			content:"今天我们创建这个群，就是要在党的生日来临之际，向公司全体共产党员致以诚挚的问候和节日的祝福！",
			dialogType:0,
		},
		{
			author:member.tangxiaoyun,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.liyue,
			content:"也希望大家借今天这个机会，敞开心扉，畅所欲言。",
			dialogType:0,
		},
		{
			author:member.shangbing,
			content:"中国移动成立18年来，能够取得今天的成绩，离不开中国共产党的正确领导，离不开公司各级党组织、全体党员和广大职工的共同努力！在此，我代表集团公司党组，向一直以来奋战在公司各条战线上的广大党员致以最诚挚的谢意！",
			dialogType:0,
		},
		{
			author:member.liyue,
			content:"感谢同志们！也希望大家在公司新时代的新征程中继续不懈努力！",
			dialogType:0,
		},
		{
			author:member.lizhengmao,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.lihuidi,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.dongxing,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.guohao,
			content:emoji.zcgjdd,
			dialogType:1,
		},
		{
			author:member.jianqing,
			content:emoji.bwcx,
			dialogType:1,
		},
		{
			author:member.wangxiaoyun,
			content:"我也表个态：努力做大连接规模、做优连接服务、做强连接应用，为人民群众提供更加优质的信息网络服务！",
			dialogType:0,
		},
		{
			author:member.guolangke,
			content:"公司的改革发展为我们广大员工的成长提供了广阔天地，我们要更加努力，不负时代、不负使命！",
			dialogType:0,
		},
		{
			author:member.zhaojing,
			content:"我要扎实做好本职工作，全心全意服务客户，以实际行动践行“以人民为中心”的理念！",
			dialogType:0,
		},
		{
			author:member.shiyuxi,
			content:"我将全身心投入到祖国边疆的网络建设中去，继续做一颗质朴坚硬的“小石头”，以实际行动为实现“中国梦”不懈奋斗！",
			dialogType:0,
		},
		{
			author:member.shangbing,
			content:"大家讲的都很好。@xxx同志，你有什么想法？",
			dialogType:0,
		},
		{
			author:member.liyue,
			content:"@xxx",
			dialogType:0,
		},
		{
			author:member.me,
			content:`好荣幸，好突然，好紧张⊙﹏⊙b${emoji.jingzhang}`,
			dialogType:0,
			time:1000
		},
		{
			author:member.me,
			content:"我很荣幸能成为伟大时代的见证者、历史进程的亲历者、公司发展的参与者，更要做为实现中华民族伟大复兴而努力奋斗的坚定者、奋进者、搏击者！",
			dialogType:0,
			time:1000
		},
		{
			author:member.shangbing,
			content:"@xxx，这位同志讲得很好，我们要不忘初心、牢记使命，奋力在新时代实现公司新发展，为实现中华民族伟大复兴的中国梦贡献更大力量！",
			dialogType:0,
		},
		{
			author:member.shangbing,
			content:"",
			dialogType:2,
		},
	];
	var timer;
	var posi=0;
	function start(){
		timer=setInterval(function(){
			data.push(dialog[posi]);
			scrollSmooth($elem,800);
			posi++;
			if(dialog[posi]&&dialog[posi].time){
				pause();
				setTimeout(function(){
					start();
				},dialog[posi].time);
			}
			if(posi>dialog.length-1){
				pause();
			}
		},1000);
	}
	function pause(){
		clearInterval(timer);
	}
	function scrollSmooth($elem,duration){
		var scrollHeight=$elem.scrollHeight;
		var startPosition = $elem.scrollTop;
		var delta = scrollHeight  - startPosition;
		var startTime = Date.now();
		function scroll() {
			var fraction = Math.min(1, (Date.now() - startTime) / duration);
			$elem.scrollTop = delta * fraction + startPosition;
			if(fraction < 1) {
				setTimeout(scroll, 10);
			}
		}
		scroll();
	}
	return {
		start,
		pause
	}
}


