<template>
	<div class="friends">
		<div class="header">
			<img src="~@/assets/friends/header.jpg">
		</div>
		<div class="content">
				<ul>
					<li v-for="item in friends" :key="item.id">
						<div class="p-info">
							<div class="head">
								<img :src="item.head">
							</div>
							<div class="other">
								<div class="name">{{item.name}}</div>
								<div class="jop">{{item.jop}}</div>
							</div>
						</div>
						<div class="express">
							{{item.express}}
						</div>
						<div class="img-show">
							<img :src="item.imgurl">
						</div>
						<div class="feekback-stats">
							<div class="wrap">
								<div class="stat" @click="zan(item)">
									<img src="~@/assets/friends/zan1.jpg">{{item.zanStats}}
								</div>
								<div class="stat stat-2">
									<img src="~@/assets/friends/comt1.jpg">{{item.commentStats}}
								</div>
							</div>
						</div>
						<div class="comment-wrap">
							<div class="comment-header">
								<i><img src="~@/assets/friends/zan2.jpg"></i>
								{{item.zanfirends}}
							</div>
							<div class="comment">
								<div class="comment-li" v-for="comment in item.comentList" :key="comment.id">
								<span class="name">{{comment.name}}</span>：
								{{comment.feedbak}}
								</div>
							</div>
						</div>	
					</li>
				</ul>	
		</div>
	</div>
</template>
<script>
	import friends from '@/data/friends'
	export default{
		data(){
			return {
				friends:friends()
			}
		},
		methods:{
			zan(item){
				if(!item.isZan){
					item.zanStats=item.zanStats+1;
					item.zanfirends="我，"+item.zanfirends;
					item.isZan=true;
				}
			}
		}
	}
</script>
<style lang="less">
	.friends{
		position: relative;
		height: 100%;
		padding-top: 2.54rem;
		.header{
			position: absolute;
			height: 2.54rem;
			width: 100%;
			top:0;
			left: 0;
			z-index: 1;
			box-shadow: 0 0 10px #ccc;
			img{
				width: 100%;
			}
		}
		.content{
			height: 100%;
			overflow: auto;
			padding-bottom: 0.3rem;
			ul{
				margin-top: 0.35rem;
			}
			li{
				position: relative;
				padding: 0.3rem;
				margin-top: 0.2rem;
				&:after{
					position: absolute;
					bottom: 0;
					content: "";
					left: 0;
					width: 100%;
					border-bottom: 1px solid #ccc;
					transform: scaleY(0.5);
				}
			}
			.p-info{
				position: relative;
				height: 1.3rem;
				.head{
					width: 1.3rem;
					height: 100%;
					float: left;
					font-size: 0;
					img{
						width: 100%;
						border-radius: 100%;
					}
				}
				.other{
					padding-left: 0.3rem;
					overflow: hidden;
					height: 100%;
					.name{
						font-size: 0.32rem;
						color: #4086f5;
						margin-top: 0.1rem;
					}
					.jop{
						margin-top: 0.2rem;
						color: #999;
					}
				}
			}
			.express{
				margin-top: 0.1rem;
				padding: 0.2rem 0;
			}
			.img-show{
				width: 2.54rem;
				padding: 0.1rem 0;
				img{
					max-width: 2.54rem;
				}
			}
			.feekback-stats{
				text-align: right;
				.wrap{
					overflow: hidden;
					display: inline-block;
					color: #b8c1c8;
					.stat{
						float: left;
					}
					.stat-2{
						margin-left: 0.5rem;
					}
					img{
						width: 0.4rem;
						vertical-align: middle;
						margin-right: 0.1rem;
					}
				}
			}
			.comment-wrap{
				background: #f3f3f3;
				margin-top: 0.2rem;
				.name{
					color: #4086f5;
				}
				.comment-header{
					position: relative;
					padding: 0.2rem;
					padding-left: 0.8rem;
					color:#4086f5;
					i{
						position: absolute;
						left: 0.2rem;
						top:0.25rem;
						z-index: 1;
						img{
							width: 0.4rem;
						}
					}
					&:after{
						position: absolute;
						bottom: 0;
						content: "";
						left: 0;
						width: 100%;
						border-bottom: 1px solid #ccc;
						transform: scaleY(0.5);
					}
				}
				.comment{
					padding: 0.3rem;
					.comment-li:first-child{
						margin-top: 0;
					}
					.comment-li{
						margin-top: 0.2rem;
					}
				}
			}
		}
	}
</style>


