<template>
	<div class="loading">
		<div class="small-gp">
			<img src="~@/assets/loading/small_gp.png">
		</div>
		<div class="phone">
			<img src="~@/assets/loading/loading_phone.png">
		</div>
		<div class="head">
			<img src="~@/assets/loading/head.png">
		</div>
		<div class="name">
			用户昵称
		</div>
		<div class="progress">{{progress}}%</div>
		<div class="roate roate-1"><img src="~@/assets/loading/roate1.png" class="roate-img"></div>
		<div class="roate roate-2"><img src="~@/assets/loading/roate2.png" class="roate-img"></div>
		<div class="roate roate-3"><img src="~@/assets/loading/roate3.png" class="roate-img"></div>
	</div>
</template>
<script>
	var imgs=['./static/member/dongxing.png','./static/member/guohao.png','./static/member/guolangke.png','./static/member/jianqing.png','./static/member/lihuidi.png','./static/member/liyue.png','./static/member/lizhengmao.png'];
	import imgLoader from '@/assets/js/img-loader';
	export default{
		data(){
			return {
				progress:0,
			}
		},
		mounted(){
			this.$nextTick(()=>{
				var vm=this;
				var basePlus=parseInt(100/imgs.length);
				var progressTimer;
				var progressNow=0;
				timer();
				imgLoader(imgs,function(eq){
					progressNow=eq*basePlus;
					if(eq<imgs.length){
						timer();
					}else{
						timer(true);
					}
				})
				function timer(end){
					clearInterval(progressTimer);
					progressTimer=setInterval(function(){
						if(vm.progress==progressNow|| vm.progress==100){
							clearInterval(progressTimer);
							if(end){
								if(vm.progress<100){
									progressNow=100;
									timer(true);
								}else{
									vm.$parent.status=1;
								}
							}
							return;
						}
						vm.progress++;
					},10);
				}
			})
		}
	}
</script>
<style lang="less">
	.loading{
		position: relative;
		height: 100%;
		background: #3a8ce4 url('~@/assets/loading/loading_bg.jpg')  no-repeat;
		background-size: 100%;
		.roate{
			position: absolute;
			z-index: 23;
			animation: circle 5s linear 0s infinite;
			transform-style:preserve-3d;
			img{
				animation:circleimg 5s linear 0s infinite;
			}
			&.roate-1{
				left: 1.14rem;
				top:5rem;
				transform-origin:2.54rem 0.9rem;
			}
			&.roate-2{
				left: 5.525rem;
				top:5rem;
				transform-origin:-1.82rem 0.9rem;
			}
			&.roate-3{
				left:5.76rem;
				top:6.7rem;
				transform-origin:-2rem -0.75rem;
			}
			.roate-img{
				width: 0.75rem;
			}
		}
		.small-gp{
			position: absolute;
			top:2.15rem;
			left: 0;
			z-index: 25;
			img{
				width: 100%;
				animation: shake 10s linear 0s infinite;
			}
		}
		.phone{
			position: absolute;
			top:3.18rem;
			left: 1rem;
			z-index: 24;
			img{
				width: 5.7rem;
			}
		}
		.head{
				position: absolute;
				left: 3.05rem;
				top:5.06rem;
				height: 1.4rem;
				width: 1.4rem;
				border-radius: 100%;
				overflow: hidden;
				z-index: 26;
				img{
					position: absolute;
					left: 0;
					top:0;
					width: 100%;
				}

			}
		.name{
				position: absolute;
				top:6.4rem;
				left:3.2rem;
				z-index:26;
				font-size: 0.28rem;
				transform: scale(0.75);
			}
		.progress{
				position: absolute;
				top:7.15rem;
				left:3.45rem;
				z-index:26;
				font-size: 0.28rem;
			}		
	}
	 @keyframes circle {  
          from { transform:rotate(0deg); }  
          to { transform:rotate(360deg); }  
      }
     @keyframes circleimg {
	  0% {
	    transform: rotate(0deg);
	  }
	  100% {
	    transform: rotate(-360deg);
	  }
	}   
</style>


