<template>
  <div id="app">
    <loading v-if="status==0"/>
    <desk v-if="status==1"/>
    <groupChat v-if="status==2"/>
    <friends v-if="status==3"/>
  </div>
</template>

<script>
import desk from './components/desk'
import groupChat from './components/group-chat'
import loading from './components/loading'
import friends from './components/friends'
export default {
  name: 'App',
  data(){
    return {
      status:0,
    }
  },
  components: {
    desk,
    groupChat,
    loading,
    friends
  },
  created(){
    document.onclick=function(e){
      e.preventDefault();
    }
  }
}
</script>

<style>
  *{
    box-sizing: border-box;
  }
  ul,li{
    list-style: none;
    margin: 0;
    padding: 0;
  }
  html,body{
    height: 100%;
    margin: 0;
  }
  body{
    font-size: 0.3rem;
    color: #333;
  }
  #app{
    height: 100%;
  }
  @keyframes hand{
    0%{
      opacity: 0;
      transform: translate(2rem,2rem);
    }
    100%{ 
      opacity: 1; 
      transform: translate(0,0);
    }
  }
  .animation-hand{
    animation:hand;
    animation-duration:2.5s;
    animation-iteration-count:infinite;
  }
 @keyframes shake{  
   0%, 100% {
        -webkit-transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70%,
    90% {
        -webkit-transform: translateX(-3px);
    }
    20%,
    40%,
    60%,
    80% {
        -webkit-transform: translateX(3px);
    }
  } 
</style>



