<template>
	<div class="wrap-chat">
		<div class="group-chat" v-if="status==2">
			<div class="header">
				<img src="~@/assets/chat_header.jpg">
				<span>{{grounpName}}</span>
			</div>
			<div class="dialog-content" ref="scroll">
				<div class="tip">
					<span>
						尚冰（党组书记 董事长），李跃（党组副书记 总裁），李正茂（党组成员 副总裁），李慧镝（党组成员 副总裁），董昕（党组成员 副总裁），郭浩（党组成员 纪检组长），简勤（党组成员 副总裁），王晓云（十九届中央候补委员 技术部总经理），赵静（十九大代表，湖北公司党员），郭兰珂（十九大代表 重庆公司党员），石玉喜（十九大代表 中移铁通公司党员），党小云，xxx（参与者微信名）”加入群
					</span>
				</div>
				<div class="tip delay" v-show="tipShow">
					<span>
						尚冰将群名改为‘庆七一 谈心声 话发展’
					</span>
				</div>
				<div :class="'dialog '+(item.author.id=='me'?'me':'friend')" v-for="item in dialog">
					<div class="contain">
						<div class="head">
							<img :src="item.author.head">
						</div>
						<div class="info">
							<div class="name">{{item.author.name}}</div>
							<div :class="'content '+(item.dialogType==1?'no-bg':'')" v-html="item.content" v-if="item.dialogType!==2">
							</div>
							<div class="content no-bg hongbao" v-else>
								<img src="~@/assets/hongbao.png" @click="go">
								<div class="hand animation-hand"><img src="~@/assets/hand.png"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="input-grounp">
				<img src="~@/assets/chat_bottom.jpg">
			</div>
		</div>
		<div class="start-mash" v-if="status==0">
			<img src="~@/assets/chat/start.jpg">
		</div>
		<div class="info-mash" v-if="status==1">
			<div class="header">
				<img src="~@/assets/chat/info_top.jpg">
			</div>
			<div class="content">
				<ul>
					<li @click="chat">
						<div class="icon">
							<img src="~@/assets/chat/zydj.png">
						</div>
						<div class="other">
							<div class="txt">中移党建<sup>刚刚</sup></div>
						</div>
					</li>
					<li>
						<div class="icon">
							<img src="~@/assets/chat/hwx.png">
						</div>
						<div class="other">
							<div class="txt">139邮箱助手<sup>昨天</sup></div>
						</div>
					</li>
					<li>
						<div class="icon">
							<img src="~@/assets/chat/hwx.png">
						</div>
						<div class="other">
							<div class="txt">和飞信团队<sup>9:30</sup></div>
						</div>
					</li>
				</ul>
				<div class="hand animation-hand"><img src="~@/assets/hand.png"></div>		
			</div>
			<div class="footer">
				<img src="~@/assets/chat/info_bottom.jpg">
			</div>
		</div>
	</div>
</template>
<script>
	import ChatDialog from '@/assets/js/chat-dialog'
	export default{
		data(){
			return{
				grounpName:"中移党建",
				tipShow:false,
				dialog:[],
				status:0
			}
		},
		mounted(){
			setTimeout(()=>{
				this.status=1;
			},1500);
		},
		methods:{
			chat(){
				this.status=2;
				this.$nextTick(()=>{
					var chatDialog=this.chatDialog=ChatDialog(this.dialog,this.$refs.scroll);
					setTimeout(()=>{
						this.grounpName='庆七一 谈心声 话发展';
						this.tipShow=true;
						chatDialog.start();
					},1500);
				})
				
			},
			go(){
				this.$parent.status=3;
			}
		}
	}
</script>
<style lang="less">
	@p1:1rem;
	@cor:0.17rem;
	@hcor:1.87*@cor;
	@head:.9rem;
	.wrap-chat{
		position: relative;
		height: 100%;
		.start-mash{
			position: absolute;
			top:0;
			left: 0;
			z-index: 111;
			width: 100%;
			height: 100%;
			background: #32c4c4;
			text-align: center;
			img{
				position: relative;
				top:2.8rem;
				width: 5.2rem;

			}
		}
		.info-mash{
			position: absolute;
			top:0;
			left: 0;
			z-index: 111;
			width: 100%;
			height: 100%;
			background: #fff;
			padding-top: 2rem;
			.header{
				position: absolute;
				top:0;
				left: 0;
				width: 100%;
				height: 2rem;
				overflow: hidden;
				img{
					width: 100%;
				}
			}
			.content{
				height: 100%;
				overflow: auto;
				ul{
					margin-top: 0.1rem;
					padding: 0.3rem;
				}
				li{
					padding-top: 0.3rem;
					height: 1.7rem;
				}
				li .icon{
					padding-right: 0.2rem;
					img{
						width: 1.2rem;
					}
					float: left;
				}
				.hand{
			        position: absolute;
			       	top:2.8rem;
			        left:3.1rem;
			        z-index: 11;
			        img{
			          width: 0.9rem;
			        }
			      }
				li .other{
					position: relative;
					height: 100%;
					overflow: hidden;
					.txt{
						font-size: 0.35rem;
					}
					sup{
						font-size: 0.28rem;
						color: #999;
						float: right;
					}
					&:after{
						position: absolute;
						bottom: 0;
						left: 0;
						width: 100%;
						border-bottom:1px solid #dedede;
						content: "";
						transform: scaleY(0.5);
					}
				}
			}
			.footer{
				position: absolute;
				bottom:0;
				left: 0;
				width: 100%;
				height: 1.28rem;
				overflow: hidden;
				img{
					width: 100%;
				}
			}
		}
	}
	.group-chat{
		position: relative;
		height: 100%;
		padding-top: 1.146rem;
		padding-bottom: 1.8rem;
		.header{
			position: absolute;
			top:0;
			left: 0;
			width: 100%;
			height: 1.146rem;
			overflow: hidden;
			z-index: 3;
			font-size: 0;
			img{
				width:100%;
			}
			span{
				position: absolute;
				top:0.1rem;
				left: 0.8rem;
				display: block;
				height: 100%;
				z-index: 11;
				font-size: 0.3rem;
				color: #fff;
				&:after{
					content: "";
					display:inline-block;
					vertical-align: middle;
					height: 100%;
				}
			}
		}
		.dialog-content{
			height: 100%;
			padding-bottom: 0.31rem;
			overflow: auto;
			background: #eeeae7;
		}
		.tip{
			text-align: center;
			padding: 0.45rem;
			padding-bottom: 0;
			span{
				font-size: 0.26rem;
				text-align: left;
				color: #fff;
				display: inline-block;
				padding:0.25rem 0.3rem;
				background: #c9c9c9;
				border-radius: 5px;
			}

		}
		.dialog{	
			padding:0 0.3rem;
			margin-top: 0.4rem;
			animation: goup .5s;
			.contain{
				position: relative;
			}
			.name{
				font-size: 0.24rem;
				padding-bottom: 0.06rem;
				color: #b0b0b0;
			}
			.head{
				position: absolute;
				top:0;
				width: @head;
				height: @head;
				border-radius: 100%;
				overflow: hidden;
				img{
					width: 100%;
				}
			}
			.content{
				position: relative;
				min-width: 1rem;
				max-width: 4.7rem;
				display: inline-block;
				padding: 0.18rem;
				border-radius: 5px;
				font-size: 0.3rem;
				background: #fff;
				line-height: 0.48rem;
				&:after{
					position: absolute;
					content: "";
					width:@cor;
					height: @hcor;
					top:0;
				}
				&.hongbao{
					position: relative;
					width: 4.6rem !important;
					overflow: hidden;
					.hand{
						position: absolute;
						left: 3rem;
						top:0.6rem;
						z-index: 11;
						img{
							width: 0.9rem;
						}
					}
					img{
						max-width: 100% !important;
						width: 100%;
					}
				}
				img{
					vertical-align: middle;
					margin: 0 0.1rem;
					width: 0.6rem;
				}
				&.no-bg{
					background: none;
					padding: 0;
					img{
						min-width: 1rem;
						max-width:3rem;
					}
					&:after{
						width: 0;
					}
				}
			}
		}
		.friend .head{
			left: 0;
		}
		.friend .content:after{
			left: -@cor;
			background: url(~@/assets/cor_friend.png) no-repeat;
			background-size: 100%;
		}
		.friend .info{
			padding-left: 0.16rem;
		}
		.friend .contain{
			padding-left: @p1;
		}
		.dialog.me{
			text-align: right;
		}
		.me .head{
			right: 0;
		}
		.friend .content{
			border-top-left-radius: 0;
		}
		.me .content{
			text-align: left;
			background: #2dbebb;
			color: #fff;
			border-top-right-radius: 0;
		}
		.me .content:after{
			right: -@cor;
			background: url(~@/assets/cor_me.png) no-repeat;
			background-size: 100%;
		}
		.me .info{
			padding-right: 0.16rem;
		}
		.me .contain{
			padding-right:@p1;
		}
	}
	.input-grounp{
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		font-size: 0;
		overflow: hidden;
		img{
			width: 100%;
		}
	}
	 @keyframes goup{
	 	0%{
	 		opacity: 0;
	      	transform: translateY(0.4rem);
	    }  
	    100%{ 
	    	opacity: 1;
	      transform: translateY(0);
	    }
	 }
</style>


